@font-face {
  font-family: "Roboto";
  src: url("./fonts/RobotoSlab-Regular.ttf");
}

@font-face {
  font-family: "Sevillana";
  src: url("./fonts/YeonSung-Regular.ttf");
}

body {
  font-family: Roboto;
  padding: 0;
  margin: 0;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: green;
  color: white;
  overflow-x: hidden;
}

.title {
  font-size: 2.5rem;
  margin: 10px;
}

.sub-head {
  display: flex;
  justify-content: space-between;
}

.sub-head > span {
  margin-left: 1vw;
}

.scores {
  display: flex;
  gap: 8px;
  margin-right: 2vw;
}

.cards {
  margin-top: 2vh;
  display: grid;
  gap: 3%;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 12vh;
  padding: 5px;
}

img {
  border-radius: 3px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 5px;
  background-color: rgb(134, 197, 148);
  border-radius: 3px;
  color: black;
  font-family: Sevillana;
}

.card:hover {
  cursor: pointer;
  box-shadow: 3px 3px 3px yellow;
  background-color: rgb(85, 128, 94);
  color: white;
}

.footer {
  display: flex;
  justify-content: center;
  background-color: black;
  padding: 8px;
}

@media screen and (max-width: 800px) {
  .cards {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
}
